'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { PharmaSureLogo } from '@/components/icons';
import { cn } from '@/lib/utils';
import { useUser } from '@/firebase';

const splashStages = {
  initial: 'scale-0 opacity-0',
  stage1: 'scale-[0.8] opacity-100', // Splash 2 (small)
  stage2: 'scale-[1.25] opacity-100', // Splash 3 (large)
  stage3: 'scale-100 opacity-100', // Splash 4/5 (final)
};

export default function SplashPage() {
  const [stage, setStage] = useState('initial');
  const [showText, setShowText] = useState(false);
  const router = useRouter();
  const { user, isUserLoading } = useUser();

  useEffect(() => {
    const sequence = async () => {
      // Start
      await new Promise(resolve => setTimeout(resolve, 100));
      setStage('stage1');

      // Grow
      await new Promise(resolve => setTimeout(resolve, 800));
      setStage('stage2');

      // Settle
      await new Promise(resolve => setTimeout(resolve, 800));
      setStage('stage3');
      
      // Show text
      await new Promise(resolve => setTimeout(resolve, 500));
      setShowText(true);

      // Navigate
      await new Promise(resolve => setTimeout(resolve, 1500));

      if (!isUserLoading) {
        if (user) {
            if(user.role === 'pharmacist') {
                router.push('/pharmacist-dashboard');
            } else if(user.profileComplete) {
                router.push('/home');
            } else {
                router.push('/setup-profile');
            }
        } else {
            router.push('/language');
        }
      }
    };

    if(!isUserLoading) {
        sequence();
    }
  }, [router, user, isUserLoading]);

  return (
    <div className="flex h-screen w-full flex-col items-center justify-center bg-background">
      <div className="flex flex-col items-center justify-center">
        <div
          className={cn(
            'transition-all duration-700 ease-in-out',
            splashStages[stage as keyof typeof splashStages]
          )}
        >
          <PharmaSureLogo
            className={cn(
              'h-24 w-24 text-primary'
            )}
          />
        </div>
        <div
          className={cn(
            'flex flex-col items-center transition-opacity duration-500',
            showText ? 'opacity-100' : 'opacity-0'
          )}
        >
          <h1 className="mt-4 font-headline text-4xl font-bold text-primary">
            PharmaSure
          </h1>
          <p className="mt-2 text-muted-foreground">
            "Smart Alerts. Safer Health."
          </p>
        </div>
      </div>
    </div>
  );
}
